// My plugin (command shift s)
const colorVariables = {
  baseColors: {
    green: {
      default: "#14B39D",
      25: "#0B8171",
      40: "#19CCB3",
      85: "#B4FFF6",
      95: "#E6FFFC",
    },
    white: {
      default: "#FFFFFF"
    },
    navy: {
      default: "#1D3D45",
      30: "#2E5768",
      40: "#407A8B",
      95: "#EEF4F6",
      98 : "#F9FBFB"
    },
    blue: {
      default: "#0090E0",
      95: "#B2E3FF",
      98: "#E5F6FF"
    },
    gray: {
      default: "#CCCCCC",
      60: "#999999",
      90: "#E6E6E6",
      95: "#F2F2F2"
    },
    orange: {
      default: "#F1690E",
      95: "#FEF0E7"
    },
    red: {
      default: "#E50051",
      95: "#FFE5EE"
    },
    purple: {
      default: "#B26DFF",
    },
    black: {
      default: "#181818",
    }
  }
}

export default function(context) {
  var selection = context.selection;
  context.document.showMessage('Plugin ðŸƒ');

  if (selection.length == 0) {
    return context.document.showMessage('ðŸ—ðŸŒˆ: Please select a layer');
  }

  if (selection[0] instanceof MSTextLayer) {
    const lay = createTextLayer('tone', 'tone: ' + 'NEGATIVE');
    // gets the position of selection
    var midX=context.selection[0].frame().midX();
    var midY=context.selection[0].frame().midY();

    // pass the position to the text layer to be inserter
    lay.frame().midX = midX;
    lay.frame().midY = midY;
    return context.document.currentPage().addLayers([lay]);
  }

  const colorMapping = getColorVariablesMapping(colorVariables)
  const currentSelectedColor = getFillHexColor(selection).toString().toUpperCase();


  const lay = createTextLayer('design', 'color: ' + colorMapping["#" + currentSelectedColor]);

  // gets the position of selection
  var midX=context.selection[0].frame().midX();
  var midY=context.selection[0].frame().midY();

  // pass the position to the text layer to be inserter
  lay.frame().midX = midX;
  lay.frame().midY = midY;

  // add the layer to the artboar
  context.document.currentPage().addLayers([lay]);
}

function getFillHexColor(selection) {
  var layer = context.selection[0]
  var selectedColor = getFillRGBColor("fill", layer)
  var colorHex = selectedColor.immutableModelObject().hexValue().toString()
  return colorHex
}

function getFillRGBColor (type, layer) {
  var color

  if (type === "fill") {
    if (layer instanceof MSTextLayer) {
      color = layer.textColor()
    } else {
      color = layer.style().fills().firstObject().color()
    }
  } else {
    color = layer.style().borders().firstObject().color()
  }

  return color
}

// Hex to Color - helper function
var hexToColor = function(hex, alpha) {
    var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex),
        red = parseInt(result[1], 16) / 255,
        green = parseInt(result[2], 16) / 255,
        blue = parseInt(result[3], 16) / 255,
        alpha = (typeof alpha !== 'undefined') ? alpha : 1;
    return NSColor.colorWithCalibratedRed_green_blue_alpha(red, green, blue, alpha)
}


function createTextLayer(name, stringValue) {
    var textLayer = MSTextLayer.new();
    textLayer.stringValue = stringValue
    textLayer.name = name
    textLayer.setTextColor = hexToColor("CCCCCC")
    return textLayer;
}

function getColorVariablesMapping(variables) {
  const obj = variables.baseColors
  const colorNames = {}
  // gets default color variables
  Object.keys(obj).map(colorGroupName => {
    const colorGroup = obj[colorGroupName]

    colorNames[colorGroup.default] = '--color-' + colorGroupName;

    Object.keys(colorGroup).forEach(variation => {
      // filters out default values
      if (variation !== 'default') {
        colorNames[colorGroup[variation]] = '--color-' + colorGroupName + '-' + variation;
      }
    })
  })

  return colorNames
}
